import React, {Component} from "react";
import PropTypes from "prop-types";
import {getNumberOfCartEntries, getHasMiniCartData, containsEmptySaleOfAddonsEntry} from "eshop/modules/cart/selectors";
import {connect} from "react-redux";

const HideWhenCartIsEmptyView = (props) => {
    if(!props.hasMiniCart || props.numberOfEntries === 0){
        return null;
    }
    return (<div className={props.className}>{props.children}</div>);
}

const HideWhenCartContainsEmptyEntriesView = (props) => {
    if(props.containsEmptySaleOfAddonsEntry){
        return null;
    }
    return (<div className={props.className}>{props.children}</div>);
}

const ShowWhenCartIsEmptyView = (props) => {
    if(props.hasMiniCart && props.numberOfEntries > 0){
        return null;
    }
    return (<div className={props.className}>{props.children}</div>);
}

const PresentBasedOnCartEmptinessView = (props) => {
    if(props.numberOfEntries && props.numberOfEntries === 0){
        return (<div className={props.classNameIfEmpty}>{props.ifEmpty}</div>);
    }
    return (<div className={props.classNameIfNotEmpty}>{props.ifNotEmpty}</div>);
}

const mapStateToProps = state => ({
    numberOfEntries: getNumberOfCartEntries(state),
    hasMiniCart: getHasMiniCartData(state),
    containsEmptySaleOfAddonsEntry: containsEmptySaleOfAddonsEntry(state)
})

