export const maleOrdinal = (number) => monthlyLabels[number];

export const loyaltyDuration = (number) => {

    if(number === 0) {
        return "Umowa w promocji na czas nieokreślony";
    }
    if(number === 1){
        return "Umowa na " + number + " miesiąc";
    }
    if ((number < 10 || number > 20) && [2,3,4].includes((number % 10))){
        return "Umowa na " + number + " miesiące";
    }
    if (number == null) {
        return "Umowa na dotychczasowy okres";
    }
    return "Umowa na " + number + " miesięcy";
}

export const HideWhenCartIsEmpty = connect(mapStateToProps,null)(HideWhenCartIsEmptyView);
export const ShowWhenCartIsEmpty = connect(mapStateToProps,null)(ShowWhenCartIsEmptyView);
export const HideWhenCartContainsEmptyEntries = connect(mapStateToProps,null)(HideWhenCartContainsEmptyEntriesView);
export const PresentBasedOnCartEmptiness = connect(mapStateToProps,null)(PresentBasedOnCartEmptinessView);
